import React, { Component, Fragment } from 'react'
import { Link, Redirect } from "react-router-dom";



import axiosCreate from './axiosCreate'
// import { createHashHistory } from 'history'
// export const history = createHashHistory()


export default class Nav extends Component {

    state = {
        email: "",
        token: false
    }


    async componentDidMount() {


        const token = localStorage.getItem('token')
        console.log("El valor del token obtenido es: ", token)


        await axiosCreate('/perfil/1', { headers: { 'token': localStorage.getItem('token') } })
            .then((res) => {

                this.setState({
                    email: res.data.email,
                    token: true
                })
            }).catch((err) => { console.log("El error es :", err) })
    }

    onSubmit = async (e) => {
        e.preventDefault();
        localStorage.removeItem('token')


    }


    render() {

        return (
            <Fragment>
                <nav className="navbar navbar-expand-lg navbar-dark fondo-nav">

                    <div className="container">
                        <Link className="navbar-brand " to="/api/perfil">App</Link>
                        <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                            aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                            <span className="navbar-toggler-icon"></span>
                        </button>
                        <div className="collapse navbar-collapse" id="navbarNav">
                            <ul className="navbar-nav">
                                <li className="nav-item mr-3">
                                    <Link className="nav-link icono-nav" to="/api/">
                                        <i class="fas fa-home"></i>
                                        <span className="sr-only">(current)</span>
                                    </Link>
                                </li>
                                <li className="nav-item mr-3">
                                    <Link className="nav-link icono-nav" to="/api/userlist">
                                        <i class="fas fa-cog"></i>
                                    </Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link icono-nav" to="/api/productlist">
                                    <i class="fas fa-shopping-cart"></i>

                                    </Link>
                                </li>
                            </ul>



                            <ul className="navbar-nav ml-auto">

                                <li className="nav-item ">
                                    <Link className="nav-link icono-nav" to="/api/signin">Iniciar Sesión</Link>
                                </li>
                                <li className="nav-item">
                                    <Link className="nav-link icono-nav" to="/api/createuser">Crear Cuenta</Link>
                                </li>




                            </ul>
                        </div>
                    </div>
                </nav>

                {/* Importar fotnawesome, para los iconos */}
                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/brands.min.css" integrity="sha512-AMDXrE+qaoUHsd0DXQJr5dL4m5xmpkGLxXZQik2TvR2VCVKT/XRPQ4e/LTnwl84mCv+eFm92UG6ErWDtGM/Q5Q==" crossorigin="anonymous" />
                <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css" integrity="sha512-1PKOgIY59xJ8Co8+NE6FZ+LOAZKjy+KY8iq0G4B3CyeY6wYHN3yt9PW0XpSriVlkMXe40PTKnXrLnZ9+fkDaog==" crossorigin="anonymous" />
            </Fragment>
        )


    }
}
