import React, { Component, Fragment } from 'react'
import { Link, Redirect } from "react-router-dom";


import axiosCreate from './axiosCreate'
import axiosCreateUser from './axiosCreateUser'
// import { createHashHistory } from 'history'
// export const history = createHashHistory()


export default class Nav extends Component {

    state = {
        email: "",
        token: false,
        accesoAdmin: false
    }


    async componentDidMount() {


        const token = localStorage.getItem('token')
        console.log("El valor del token obtenido es: ", token)


        await axiosCreate('/perfil/1', { headers: { 'token': localStorage.getItem('token') } })
            .then((res) => {

                this.setState({
                    email: res.data.email,
                    token: true,
                    accesoAdmin: true
                })
            }).catch((err) => { console.log("El error es :", err) })

        await axiosCreateUser('/perfil/1', { headers: { 'token': localStorage.getItem('token') } })
            .then((res) => {

                this.setState({
                    email: res.data.email,
                    token: true
                })
            }).catch((err) => { console.log("El error es :", err) })


    }

    onSubmit = async (e) => {
        e.preventDefault();
        localStorage.removeItem('token')
        localStorage.removeItem('borrarusuario')
    }


    render() {
        if (this.state.accesoAdmin) {
            return (
                <Fragment>
                    <nav className="navbar navbar-expand-lg navbar-dark bg-danger">
                        <div className="container">
                            <Link className="navbar-brand " to="/api/perfil">App</Link>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                                aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarNav">
                                <ul className="navbar-nav">
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/">
                                            <i class="fas fa-home"></i>
                                            <span className="sr-only">(current)</span>
                                        </Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/informacion">
                                            <i class="fas fa-cog"></i>
                                        </Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/productlist">
                                            <i class="fas fa-shopping-cart"></i>
                                        </Link>
                                    </li>
                                    <li className="nav-item">
                                        <Link className="nav-link icono-nav" to="/api/userlist">
                                            <i class="fas fa-users"></i>
                                        </Link>
                                    </li>
                                </ul>
                                <ul className="navbar-nav ml-auto">


                                    <li className="nav-item dropdown ">
                                        <Link className="nav-link dropdown-toggle" to="/" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                            {this.state.email}
                                        </Link>
                                        <div className="dropdown-menu " aria-labelledby="navbarDropdown">
                                            <button className="dropdown-item nav-user">
                                                <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-person-fill d-inline-flex" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                    <path fill-rule="evenodd" d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                                                </svg>
                                                <Link className="dropdown-item nav-user d-inline-flex" to="/api/perfil">

                                                    Ver perfil
                                                </Link>
                                            </button>
                                            <div className="dropdown-divider"></div>

                                            <form onSubmit={this.onSubmit}>

                                                <button type="submit" className="dropdown-item nav-user " onClick={this.onSubmit}>
                                                    <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-arrow-right-circle-fill d-inline-flex" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                        <path fill-rule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-11.5.5a.5.5 0 0 1 0-1h5.793L8.146 5.354a.5.5 0 1 1 .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 8.5H4.5z" />
                                                    </svg>
                                                    <Link className="dropdown-item nav-user d-inline-flex" to="/api/" type="submit">Cerrar sesión</Link>
                                                </button>
                                            </form>
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </nav >
                    {/* Importar fotnawesome, para los iconos */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/brands.min.css" integrity="sha512-AMDXrE+qaoUHsd0DXQJr5dL4m5xmpkGLxXZQik2TvR2VCVKT/XRPQ4e/LTnwl84mCv+eFm92UG6ErWDtGM/Q5Q==" crossorigin="anonymous" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css" integrity="sha512-1PKOgIY59xJ8Co8+NE6FZ+LOAZKjy+KY8iq0G4B3CyeY6wYHN3yt9PW0XpSriVlkMXe40PTKnXrLnZ9+fkDaog==" crossorigin="anonymous" />

                </Fragment >
            )
        } else {
            return (
                <Fragment>
                    <nav className="navbar navbar-expand-lg navbar-dark fondo-nav">
                        <div className="container">
                            <Link className="navbar-brand " to="/api/perfil">App</Link>
                            <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarNav"
                                aria-controls="navbarNav" aria-expanded="false" aria-label="Toggle navigation">
                                <span className="navbar-toggler-icon"></span>
                            </button>
                            <div className="collapse navbar-collapse" id="navbarNav">
                                <ul className="navbar-nav">
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/">
                                            <i class="fas fa-home"></i>
                                            <span className="sr-only">(current)</span>
                                        </Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/informacion">
                                            <i class="fas fa-cog"></i>
                                        </Link>
                                    </li>
                                    <li className="nav-item mr-3">
                                        <Link className="nav-link icono-nav" to="/api/productlist">
                                            <i class="fas fa-shopping-cart"></i>
                                        </Link>
                                    </li>
                                </ul>
                                <ul className="navbar-nav ml-auto">


                                    <li className="nav-item dropdown ">
                                        <Link className="nav-link dropdown-toggle" to="/" id="navbarDropdown" role="button" data-toggle="dropdown" aria-haspopup="true" aria-expanded="false">
                                            {this.state.email}
                                        </Link>
                                        <div className="dropdown-menu " aria-labelledby="navbarDropdown">
                                            <button className="dropdown-item nav-user">
                                                <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-person-fill d-inline-flex" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                    <path fill-rule="evenodd" d="M3 14s-1 0-1-1 1-4 6-4 6 3 6 4-1 1-1 1H3zm5-6a3 3 0 1 0 0-6 3 3 0 0 0 0 6z" />
                                                </svg>
                                                <Link className="dropdown-item nav-user d-inline-flex" to="/api/perfil">

                                                    Ver perfil
                                                </Link>
                                            </button>
                                            <div className="dropdown-divider"></div>

                                            <form onSubmit={this.onSubmit}>

                                                {/* <Link to="/Joel" type="submit">Cerrar</Link> */}
                                                <button type="submit" className="dropdown-item nav-user " onClick={this.onSubmit}>
                                                    <svg width="1em" height="1em" viewBox="0 0 16 16" className="bi bi-arrow-right-circle-fill d-inline-flex" fill="currentColor" xmlns="http://www.w3.org/2000/svg">
                                                        <path fill-rule="evenodd" d="M16 8A8 8 0 1 1 0 8a8 8 0 0 1 16 0zm-11.5.5a.5.5 0 0 1 0-1h5.793L8.146 5.354a.5.5 0 1 1 .708-.708l3 3a.5.5 0 0 1 0 .708l-3 3a.5.5 0 0 1-.708-.708L10.293 8.5H4.5z" />
                                                    </svg>
                                                    <Link className="dropdown-item nav-user d-inline-flex" to="/api/" type="submit">Cerrar sesión</Link>
                                                </button>
                                            </form>

                                            {/* <button type="submit" className="dropdown-item" onClick={this.onSubmit}  >Cerrar Sesión</button> */}
                                            {/* btn btn-dark nav-link */}
                                        </div>
                                    </li>
                                </ul>
                            </div>
                        </div>
                    </nav >
                    {/* Importar fotnawesome, para los iconos */}
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/brands.min.css" integrity="sha512-AMDXrE+qaoUHsd0DXQJr5dL4m5xmpkGLxXZQik2TvR2VCVKT/XRPQ4e/LTnwl84mCv+eFm92UG6ErWDtGM/Q5Q==" crossorigin="anonymous" />
                    <link rel="stylesheet" href="https://cdnjs.cloudflare.com/ajax/libs/font-awesome/5.14.0/css/all.min.css" integrity="sha512-1PKOgIY59xJ8Co8+NE6FZ+LOAZKjy+KY8iq0G4B3CyeY6wYHN3yt9PW0XpSriVlkMXe40PTKnXrLnZ9+fkDaog==" crossorigin="anonymous" />

                </Fragment >

            )
        }


    }
}
